@import "__animations";
@import "__mixin";
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap');
* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  width: 100vw;
  #root {
    height: 100%;
    width: 100%;
  }
  .One {
    position: relative;
    background-color: rgb(0, 213, 255);
    width: 100vw;
    height: 100vh;
    .Name{
      @include Flex(column,wrap,center,center);
      background-color: transparent;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 40%; 
      height: 20%;
      transform: translate(-50%,-50%);
      border: black solid 1px;
      z-index: 1;
       font-family: 'Oswald', sans-serif;
       transition: height 3s, width 3s linear;
       @media (max-width: 600px) {
        min-width: 75%;
        min-height: 20%;
       }
      .Background{
        position: absolute;
        height: 100%;
        width: 100%;
        background-color: white;
        z-index: -1;
        opacity: .2;
      }
      .First{
        font-size: 1.25em;
        transition: opacity 2s 2s linear;
      }
      .Job{
        transition: opacity 2s 2s linear;
      }
      .Last{
        font-size: 1.25em;
        transition: opacity 2s 2s linear;
      }
      .Github{
        position: absolute;
        top: 120%;
        left: 0%;
        height: 25%;
        min-width: 8.5%;
        transition: all 1s;
        .GithubBack{
          position: absolute;
          background-color: white;
          opacity: .2;
          border-radius: 50%;
          height: 100%;
          width: 100%;
        }
        img{
          position: relative;
          width: 100%;
          height: 100%;
        }
      }
      .LinkedIn{
        position: absolute;
        top: 120%;
        left: 46.5%;
        height: 25%;
        min-width: 8.5%;
        transition: all 1s;
        .LinkedInBack {
          position: absolute;
          background-color: white;
            opacity: .2;
            border-radius: 50%;
            height: 100%;
            width: 100%;
        }
        img {
            position: relative;
            width: 100%;
            height: 100%;
          }
      }
      .Email{
        position: absolute;
        top: 120%;
        left: 90%;
        height: 25%;
        min-width:8.5%;
        transition: all 2s;
        .EmailBack {
          position: absolute;
          background-color: white;
            opacity: .2;
            border-radius: 50%;
            height: 100%;
            width: 100%;
            &:hover{
              color: rgba(255, 255, 255, 0.801);
              opacity: .6;
            }
        }
        @media (max-width:340px) {
          min-width: 14%
        }

        img {
            position: relative;
            width: 100%;
            height: 100%;
          }
      }
      
    }
    svg {
      display: block;
      position: relative;
      height: 100%;
      width: 100%;
      transition: all 2s;  
      #Wheat {
        position: relative;
        transform: scale(0.5);
        animation: Bread 6s infinite forwards;
      }
      rect{
        height: 55%;
        width: 100%;
        transform: translate(0%,45%);
        fill: #fff775;
      }
      .Tree{
        position: absolute;
        transform: scale(.2) translate(130%,-30%);
        #BottomLeaf{
          position: absolute;
          display: block;
          animation: BottomLeaf 5s 1s forwards infinite;
        }
        #TopLeaf{
          position: absolute;
          display: block;
          animation: TopLeaf 5s 1s forwards infinite ;
        }

      }
      .Cloud{
          transform: scale(.2);
          animation: Cloud 100s linear forwards infinite;
      }
    }
    .Works{
      @include Flex(column,wrap,center,center);
      position: absolute;
      top:80%;
      left:50%;
      width: 60%;
      height: 10%;
      z-index: 1;
      transform: translate(-50%,-50%);
      border: 1px solid black;
      transition: all 2s;
      .WorkTitle{
        z-index: 1;
        width: 100%;
          height: 25%;
          text-align: center;
      }
      .WorksBack{
        position: absolute;
        opacity: .2;
        background-color: white;
        height: 100%;
        width:100%;
        z-index: 0;
      }
      .Links{
        @include Flex(row,wrap,space-evenly,center);
        height: 75%;
        max-width: 100%;
        .TicToc{
          @include Flex(column, wrap, center, center);
          position: relative;
          max-width: 5.5%;
          max-height: 50%;
          @media (max-width: 600px) {
              max-width: 10%;
            }
          div{
            position: absolute;
            background-color:white ;
            opacity: .2;
            height: 100%;
            width: 100%;
          }
          img{
            width: 100%;
            height: 100%;
            z-index: 1;
          }

        }
        .Recipe{
          @include Flex(column,wrap,center,center);
          position: relative;
          max-width: 5.5%;
          max-height: 50%;
          @media (max-width: 600px) {
              max-width: 10%;
            }
          div {
              position: absolute;
              height: 100%;
              width: 100%;
              background-color: white;
              opacity: .2;
              z-index: 1;
            }
          img {
            height: 100%;
            width: 100%;
            z-index: 1;
            }

        }

      }
    }
  }
}

.WindLeft {
  animation: Bread 6s infinite forwards;
}
.WindRight {
  animation: Bread 6s infinite forwards;
}
