@keyframes Bread {

    0%{
        transform: rotate(0deg) scale(.25);;
        transform-origin: bottom;}
   50% {
            transform: rotate(5deg) scale(.25);
            transform-origin: bottom;}
    100%{
    transform: rotate(0deg) scale(.25);
        transform-origin: bottom;
    }
}

@keyframes Cloud {
    0%{

        transform: translate(0%) scale(.2);
    }
    100%{
        transform: translate(100%) scale(.2);
    }
}

@keyframes TopLeaf {
    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.01) translateX(.25%);
    }
    100%{
        transform: scale(1);
    }
}
@keyframes BottomLeaf {
    0% {
            transform: scale(1);
        }
    
        50% {
            transform: scale(1.01) translateX(.25%);
        }
    
        100% {
            transform: scale(1);
        }
}
